<template>
  <div id="app">
    <div id="top-special-offer">
      <h3>SPECIAL OFFER: 20% OFF BOOKS WITH WORDS IN THEM!!! LIMITED TIME ONLY!</h3>
    </div>
    <hr />    
    <div class="topbar">
      <img src="static/logo-horizontal.png" />
      <input type="text" name="fname" />
      <button id="button-search">Search</button>
      <p id="clock">3:43pm 23/06/2002</p>
      <div class="account">
        <button id="login">Log in</button>
        <button id="signup" class="call-to-action">Sign up</button>
      </div>
    </div>
    <hr />
    <div class="main-container">
      <router-view name="componentShop"></router-view>
      <router-view name="componentDetail"></router-view>
      <router-view name="componentSidebar"></router-view>
    </div>
  </div>
</template>

<script>
import Shop from "./components/Shop";
import Sidebar from "./components/Sidebar";
import Detail from "./components/Detail";

export default {
  name: "App",
  data: {
    dateNow: ''
  },
  methods: {
    time() {
      var self=this
      this.dateNow = moment().format()

      setInterval(self.time, 1000)
    },
  },
  mounted: function() {
    this.time()
  },
  components: {
    "component-sidebar": Sidebar,
    "component-Shop": Shop,
    "component-detail": Detail
  }
};
</script>

<style>
.main-container {
  display: flex;
}
.account {
  align-self: center;
  margin-left: 20pt;
  align-content: center;
}
.topbar {
  display: flex;
  height: 30pt;
}
input {
  margin-left: 40pt;
  flex-grow: 1;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 10px;
  margin-left: 10px;
}
#top-special-offer{
  background-color: #1d3557;
  padding: 7pt 0;
}
#clock {
  align-self: center;
  margin-left: 5pt;
}
h3 {
  text-align: center;
  color: #fff;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
}
img {
  width: 180pt;
  height: 30pt;
}
button {
  border: none;
  background: #1d3557;
  color: #ffffff !important;
  padding: 10pt;
  text-transform: uppercase;
  margin-right: 10pt;
}
.call-to-action {
  background: #e63946;
}
#button-search {
  
}
</style>



